.bienvenida {
  background-color: transparent;
  padding-bottom: 4rem;
  padding-top: 4rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  width: 100%;
  min-height: 100%;
  height: max-content;
  z-index: -1;
}
@media (min-width: 974px) {
  .bienvenida {
    padding-bottom: 10rem;
    padding-top: 14rem;
  }
}
.bienvenida__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  width: 100%;
  gap: 2rem;
}
@media (min-width: 974px) {
  .bienvenida__grid {
    width: 70%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto;
  }
}
.bienvenida__boxImagen {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 974px) {
  .bienvenida__boxImagen {
    display: block;
  }
}
.bienvenida__imagen {
  background-color: transparent;
  overflow: hidden;
  width: 50%;
  animation: slideinUno 3s infinite alternate;
  animation: slideinDos 3s infinite alternate;
  transform: scale(0.4);
  opacity: 0;
  transition-property: transform opacity;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
@media (min-width: 896px) {
  .bienvenida__imagen {
    width: 25rem;
  }
}
.bienvenida__imagen.activo {
  transform: scale(1);
  opacity: 1;
}
@keyframes slideinUno {
  from {
    border-radius: 20rem 23rem 20rem 40rem;
  }
  to {
    border-radius: 45rem 26rem 53rem 33rem;
  }
}
@keyframes slideinDos {
  from {
    border-radius: 45rem 26rem 53rem 33rem;
  }
  to {
    border-radius: 20rem 23rem 20rem 40rem;
  }
}
.bienvenida__imagen:hover {
  cursor: pointer;
}
.bienvenida__titulo {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  position: relative;
  margin: 0 5%;
  top: 10rem;
  transform: scale(0.8);
  opacity: 0;
  transition-property: top transform opacity;
  transition-duration: 0.7s;
  transition-timing-function: ease-in-out;
}
.bienvenida__titulo.activo {
  top: 0rem;
  opacity: 1;
  transform: scale(1);
}
@media (min-width: 896px) {
  .bienvenida__titulo {
    align-items: flex-end;
    width: unset;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 6rem;
  }
}
.bienvenida__boxProfesion {
  overflow: hidden;
  position: relative;
  padding-right: 0.2rem;
  border-right: 3px solid #707377;
  animation: blink 0.5s infinite step-end alternate;
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}
.bienvenida__spanProfesion {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  left: 0rem;
  animation: typing 7s steps(20) infinite alternate;
}
@keyframes typing {
  25% {
    left: 0rem;
  }
  80% {
    left: 18rem;
  }
}
.bienvenida__H2 {
  width: 100%;
  text-align: center;
}
@media (min-width: 896px) {
  .bienvenida__H2 {
    text-align: end;
  }
