@use 'base/variables' as v;
@use 'base/mixins' as m;

.about {
    background-color: v.$colorFondoDiv;
    padding-top: v.$separacion;
    padding-bottom: 6rem;

    &__info {
        margin: 0 auto;
        width: 75%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include m.telefonoXXL {
            margin: 0 auto;
            width: 75%;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 3rem;
        }
    }

    &__aboutMe {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2.4rem;

        @include m.telefonoXXL {
            align-items: flex-start;
        }
    }

    &__H4 {
        margin-bottom: 2rem;
        position: relative;
    }

    &__parrafoAbout {
        color: v.$colorParrafo;
    }

    &__parrafo {
        font-size: 1rem;
        color: v.$colorParrafoDos;
        font-weight: 500;
    }

    &__skylls {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__skyllsBoxBarras {

        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-bottom: 2rem;
        width: 100%;

    }

    &__stats {
        width: 85%;
        margin: 0 auto;
        margin-top: 3rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
        align-items: center;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 4rem;
        }
    }

    &__statsBox {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    &__statsH4 {
        font-size: 2rem;
        font-family: v.$fuenteTitulo;
        font-weight: 400;
        margin: 0;

        @include m.tablet {
            font-size: 4rem;
        }

        &.plus {
            position: relative;

            &:nth-child(1)::after,
            &:nth-child(2)::after {
                content: "+";
                position: absolute;
                top: -.2rem;
                font-weight: 600;
                font-size: 1.6rem;

                @include m.tablet {
                    font-size: 2.6rem;
                }
            }
        }
    }

    &__parrafoStats {
        text-align: center;
        color: v.$colorParrafoDos;
        font-weight: 400;
    }
}