@use 'base/variables' as v;
@use 'base/mixins' as m;

body {
    background-color: v.$colorFondoBody;
    user-select: none;
    overflow-x: hidden;
}

.cuerpo {
    overflow-x: hidden;
}

p {
    font-family: v.$fuenteParrafo;
    margin-top: 0;
    margin-bottom: 0;
}

a {
    text-decoration: none;
}

[class*="__H1"] {
    font-family: v.$fuenteTitulo;
    font-size: 2.2rem;
    font-weight: 700;
    padding: 0;
    margin: 0 auto;
}

[class*="__H2"] {
    font-family: v.$fuenteTitulo;
    font-size: 2.2rem;
    font-weight: 700;
    line-height: 1.3;
    padding: 0;
    margin: 0;

    @include m.tablet {
        font-size: 4.2rem;
    }
}

[class*="__H3"] {
    font-family: v.$fuenteTitulo;
    font-size: 2rem;
    font-weight: 500;
    padding: 0;
    margin: 0;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 2.5rem;
        width: 76.66%;
        height: .2rem;
        background-color: v.$colorBtn;
    }
}

[class*="__H4"] {
    font-family: v.$fuenteTitulo;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.5;
    padding: 0;
    margin: 0;
}

[class*="__H5"] {
    font-family: v.$fuenteTitulo;
    font-size: 1.2rem;
    font-weight: 600;
    line-height: 1.5;
    margin: 0;
    padding: 0;
}

[class*="__H6"] {
    font-family: v.$fuenteTitulo;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.5;
    margin: 0;
    padding: 0;
}

[class*="__parrafo"] {
    font-family: v.$fuenteParrafo;
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.5;
    padding: 0;
}

[class*="__spanProfesion"] {
    font-family: v.$fuenteTituloProfesion;
    color: v.$colorParrafoTres;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.5;
    padding: 0;

    @include m.tablet {
        font-size: 1.8rem;
    }
}



[class*="__tel"] {
    font-family: v.$fuenteTitulo;
    color: v.$colorTitulo;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.5;
    padding: 0;
}

.colorTitulo {
    color: v.$colorTitulo;
}

.colorTituloDos {
    color: v.$colorTituloDos;
    font-family: v.$fuentePrincipal;
}

.colorTituloTres {
    color: v.$colorTituloTres;
}

.colorLinks {
    color: v.$colorParrafo;
    font-family: v.$fuentePrincipal;
    font-size: 1rem;

    @include m.telefonoXXL {
        color: v.$colorLinks;
    }
}

.colorSubTitulo {
    color: v.$colorSubTitulo;
    font-family: v.$fuentePrincipal;
}

.colorParrafo {
    color: v.$colorParrafo;
    font-family: v.$fuenteParrafo;
}

.colorParrafoDos {
    color: v.$colorParrafoDos;
    font-family: v.$fuenteParrafo;
}

.colorParrafoTres {
    color: v.$colorParrafoTres;
    font-family: v.$fuenteParrafo;
}

.colorPaCopyright {
    color: v.$colorPaCopyright;
    font-family: v.$fuentePrincipal;
}

.btnGeneralUno,
.btnGeneralDos {
    width: max-content;
    padding: 0.6rem 1.6rem;
    border: 1px solid transparent;
    font-size: 1.4rem;
    font-weight: 900;
    font-family: v.$fuentePrincipal;

    &:hover {
        cursor: pointer;
    }
}

.btnGeneralUno {
    background-color: v.$colorBtn;
    color: v.$colorTextoBtn;
}

.btnGeneralDos {
    background-color: v.$colorFondoDiv;
    color: v.$colorTextoBtnDos;
    border-color: v.$colorBtn;
}

.btnNavOpc {
    background-color: transparent;
    border: none;
    font-size: 1.6rem;
    font-family: v.$fuenteParrafo;
    color: v.$colorParrafoDos;
    font-weight: 700;
}

.btnPortfolio {
    padding: .4rem 1rem;
}

.navegadorRedes {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    gap: 1.6rem;
    align-items: center;
    align-items: center;
    width: min-content;

    &__links {
        background-color: v.$colorBtn;
        padding: .8rem;
        border-radius: 100%;
        color: v.$colorTextoBtn;
        font-size: 1.6rem;
        transition: box-shadow .4s ease-in-out;

        &:hover {
            box-shadow: 0 0 30px v.$colorBtn;
        }

    }
}


.barra {
    display: grid;
    grid-template-columns: 1fr 1px;
    align-items: center;
    column-gap: 1rem;

    // margin: .7rem 0;
    padding: 0;
    border-radius: 4px;
    background-color: lighten(v.$grisClaro, 10);

    &.temaClaro {
        background-color: darken(v.$colorBtn, 20);
    }

    :root {
        --habilidades-bar-lenght: 0%;
    }

    &__bar,
    &__bar::after {
        position: relative;
        background-color: v.$colorFondoOscuro;
        width: 100%;
        height: 9px;
        // border-radius: 8px;
        overflow: hidden;
    }

    &__bar.activo {
        &::after {
            content: "";
            position: absolute;
            left: -100%;
            background-color: v.$colorBtn;
            transition: 0.3s ease-in-out;
            animation: habilidades-barra 1.5s ease-in-out forwards;
        }
    }

    &__bar--0::after {
        --habilidades-bar-lenght: 0%;
    }

    &__bar--5::after {
        --habilidades-bar-lenght: 5%;
    }

    &__bar--10::after {
        --habilidades-bar-lenght: 10%;
    }

    &__bar--15::after {
        --habilidades-bar-lenght: 15%;
    }

    &__bar--20::after {
        --habilidades-bar-lenght: 20%;
    }

    &__bar--25::after {
        --habilidades-bar-lenght: 25%;
    }

    &__bar--30::after {
        --habilidades-bar-lenght: 30%;
    }

    &__bar--35::after {
        --habilidades-bar-lenght: 35%;
    }

    &__bar--40::after {
        --habilidades-bar-lenght: 40%;
    }

    &__bar--45::after {
        --habilidades-bar-lenght: 45%;
    }

    &__bar--50::after {
        --habilidades-bar-lenght: 50%;
    }

    &__bar--55::after {
        --habilidades-bar-lenght: 55%;
    }

    &__bar--60::after {
        --habilidades-bar-lenght: 60%;
    }

    &__bar--65::after {
        --habilidades-bar-lenght: 65%;
    }

    &__bar--70::after {
        --habilidades-bar-lenght: 70%;
    }

    &__bar--75::after {
        --habilidades-bar-lenght: 75%;
    }

    &__bar--80::after {
        --habilidades-bar-lenght: 80%;
    }

    &__bar--85::after {
        --habilidades-bar-lenght: 85%;
    }

    &__bar--90::after {
        --habilidades-bar-lenght: 90%;
    }

    &__bar--95::after {
        --habilidades-bar-lenght: 95%;
    }

    &__bar--100::after {
        --habilidades-bar-lenght: 100%;
    }
}

@keyframes habilidades-barra {
    to {
        transform: translateX(var(--habilidades-bar-lenght))
    }
}


.material-symbols-outlined {
    font-variation-settings:
        "FILL" 0,
        "wght" 500,
        "GRAD" 0,
        "opsz" 48;
}


.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 3rem;
    width: 70%;

    &__Input {
        box-shadow: none;
        background-color: unset;
        height: 2rem;
        padding: .3rem .7rem;
        border: 2px solid v.$colorBtn;
        border-radius: 2px;
        background-color: v.$colorFondoBody;
        transition: border-color 0.2s ease-in-out;
        transition: background-color .2s ease-in-out;
        outline: none;
        width: 90%;

        &:focus {
            box-shadow: 0px 0px .1rem .2rem v.$colorBtnTransparente;
            background-color: v.$colorFondoDiv;
        }
    }

    textarea {
        height: 10rem;
    }
}