@use 'base/variables' as v;
@use 'base/mixins' as m;

.portfolio {
    width: 95%;
    margin: 0 auto;
    padding: v.$separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5rem;

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        // grid-template-rows: 1rem;
        gap: .6rem;
    }

    &__articleBox {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: .2rem;
    }

    &__cuadroImagen,
    &__imagen {
        width: 100%;
        filter: grayscale(10%);

        &:hover {
            filter: grayscale(0%);
            cursor: pointer;
        }
    }

    &__cuadroImagen {
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__imagen {
        transition: transform .4s ease-in-out;

        &:hover {
            transform: scale(1.1);
        }
    }

    &__H6 {
        color: v.$colorTitulo;
        position: relative;
    }

    &__parrafo {
        columns: v.$colorParrafo;
        font-weight: 400;
    }
}

#PortfolioArticle-1,
#PortfolioArticle-2,
#PortfolioArticle-3,
#PortfolioArticle-4,
#PortfolioArticle-5,
#PortfolioArticle-6,
#PortfolioArticle-7,
#PortfolioArticle-8 {
    height: min-content;
    max-height: 42rem;
}

#PortfolioArticle-1 {
    grid-column: 1/2;
    grid-row: 1/3;
}

#PortfolioArticle-2 {
    grid-column: 2/3;
    grid-row: 1/5;
}

#PortfolioArticle-3 {
    grid-column: 3/4;
    grid-row: 1/6;
}

#PortfolioArticle-4 {
    grid-column: 1/2;
    grid-row: 3/6;
}

#PortfolioArticle-5 {
    grid-column: 2/3;
    grid-row: 5/9;
}

#PortfolioArticle-6 {
    grid-column: 3/4;
    grid-row: 6/10;
}

#PortfolioArticle-7 {
    grid-column: 1/2;
    grid-row: 6/10;
}

#PortfolioArticle-8 {
    grid-column: 2/3;
    grid-row: 9/11;
}