@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonials {
    background-color: v.$colorFondoDiv;
    padding: v.$separacion 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: v.$separacion;

    &__boxGeneral {
        display: flex;
        flex-direction: row;
        position: relative;
        width: 100%;
        right: 0%;

        transition: right .4s ease-in-out;

        &.right0 {
            right: 0%;
        }

        &.right100 {
            right: 100%;
        }

        &.right200 {
            right: 200%;
        }

    }

    &__article {
        height: 26rem;
        min-width: 90%;
        padding: 0 5%;
        display: grid;
        grid-template-columns: 2fr 1fr;
        align-items: center;
        // gap: 2rem;
    }

    &__infoText {
        width: 66%;
        margin: 0 auto;
        font-family: v.$fuentePrincipal;
        color: v.$colorParrafo;
    }

    &__parrafoNombre {
        color: v.$colorParrafoTres;
        font-weight: 900;
        margin-left: .4rem;
    }

    &__boxCuadroImagen {
        width: 13rem;
        height: 13rem;
        border-radius: 0.8rem;
        // border: 1px solid v.$grisClaro;
    }

    &__imagen {
        position: relative;
        border-radius: 0.8rem;
        width: 13rem;
        height: 13rem;
        box-shadow: 0 0 1.2rem -0.4rem v.$colorBtn;
    }

    &__navSelectorArticle {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        gap: .4rem;
    }

    &__navSelectorArticleBtn {
        background-color: darken(v.$colorBtnDos, 6);
        width: 2rem;
        height: .4rem;
        border: none;
        transition: width .3s ease-in-out;

        &.activo {
            background-color: v.$colorBtn;
            width: 3.5rem;
        }

        &:hover {
            cursor: pointer;
        }
    }
}