@use 'base/variables' as v;
@use 'base/mixins' as m;


.services {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
    padding: v.$separacion 0;

    &__boxGeneral {
        background-color: darken(v.$colorFondoDiv, 10);
        padding: 0;
        // width: max-content;
        margin: 0 auto;

        @include m.telefonoXXL {
            width: 85%;
        }
    }

    &__boxGeneralGrid {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        @include m.telefonoXXL {
            display: grid;
            width: 100%;
            margin: 0 auto;
            grid-template-columns: repeat(3, 1fr);
            gap: .1rem;
        }
    }

    &__articleBox {
        background-color: v.$colorFondoBody;
        padding: 1.5rem 2.1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    &__parrafo {
        color: v.$colorParrafo;
        text-align: center;
    }

    & .material-symbols-outlined {
        font-size: 2.4rem;
    }
}