@use 'base/variables' as v;
@use 'base/mixins' as m;

.education {
    background-color: v.$colorFondoDiv;
    width: 100%;
    margin: 0 auto;
    padding: v.$separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5rem;

    &__article {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 95%;
        border-bottom: 3px solid v.$grisClaro;
        padding-bottom: 5rem;
        gap: 3rem;

        &:last-of-type {
            border-bottom: none;
            padding-top: 0;
            padding-bottom: 0;
        }

        @include m.telefonoXXL {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            align-items: center;
        }
    }

    &__parrafo {
        color: v.$colorParrafo;
        // text-align: start;
        margin-bottom: 0;
    }

    &__H6Educacion {
        font-family: v.$fuenteTitulo;
        font-size: 1.2rem;
        width: max-content;
    }

    &__parrafoEducacion {
        margin: 0;
        width: 16rem;
        color: v.$colorParrafo;

        @include m.telefonoXXL {
            margin: 0 0 0 auto;
        }
    }
}