@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact {
    background-color: v.$colorFondoDiv;
    padding: v.$separacion 2.5rem;
    width: 95%;
    display: flex;
    flex-direction: column;
    align-items: center;
    // gap: v.$separacion;

    &__nav {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 1rem;
        margin-top: 5rem;
    }

    &__article {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.4rem;
        text-decoration: none;
        color: v.$colorParrafoDos;
    }
}