@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-color: v.$colorFondoBody;
    padding: 0.5rem 1.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    left: 0;
    right: 0;
    position: fixed;
    z-index: 1;
    transition: padding .4s ease-in-out;

    @include m.tablet {
        padding: 1.5rem 4.5rem;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;
    }

    &__H1 {
        position: absolute;
        width: 100%;
        left: 0;
        top: -.1rem;
        text-align: center;

        @include m.telefonoXXL {
            position: unset;
            width: unset;
            left: unset;
        }
    }

    #NmrTel {
        display: none;

        @include m.tablet {
            display: block;
        }
    }

    &.activo {
        padding: .6rem 1.5rem;
    }

    &__tel {
        margin-left: auto;
    }

    &__nav {
        background-color: v.$colorFondoDiv;
        position: fixed;
        top: 0;
        left: -20rem;
        width: 14rem;
        height: 100%;

        &.activo {
            left: 0;
        }
    }

    &__boxClose {
        position: relative;
    }

    &__navGrid {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
        margin: 6rem 0 auto 0;
    }
}

#CloseMenuHeader {
    position: absolute;
    font-size: 2.2rem;
    top: 1rem;
    right: 1rem;
    font-weight: 900;
}