@use 'base/variables' as v;
@use 'base/mixins' as m;

.faq {
    padding: v.$separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: v.$separacion;

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
    }

    &__article {
        width: 46%;
        height: 4rem;
        overflow: hidden;
        border-bottom: 1px solid v.$grisClaro;
        transition: height .3s ease-in-out;

        &.activo {
            height: 16rem;
        }
    }

    &__header,
    &__footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: .4rem .8rem;
    }

    &__header {

        &:hover {
            cursor: pointer;
        }
    }

    &__footer {
        // visibility: hidden;

        &.activo {
            visibility: visible;
        }
    }

    &__H6 {
        font-size: 0.6rem;
        color: v.$colorParrafoDos;

        @include m.telefonoXXL {
            font-size: 1rem;
        }
    }

    &__parrafo {
        color: v.$colorParrafo;
        padding: 1rem 0;
    }
}

#SpanExpand1,
#SpanExpand2,
#SpanExpand3,
#SpanExpand4 {
    transform: rotate(180deg);
    transition: transform .4s ease-in-out, color .4s ease-in-out;

    &.activo {
        transform: rotate(0deg);
        color: v.$colorBtn;
    }
}